import React, { Component } from 'react'

const TableHeader = (props) => {

    return (
        <thead>
            <tr>
                <th>Name</th>
                <th>Sunday</th>
                <th>Monday</th>
                <th>Tuesday</th>
                <th>Wednesday</th>
                <th>Thursday</th>
                <th>Friday</th>
                <th>Saturday</th>
            </tr>
        </thead>
    );
}

const TableBody = props => {
    const rows = (props.recordData !== undefined) ? (props.recordData.map((row, index) => {
        return (
            <tr key={index}>
                <td>{row.Name}</td>
                <td>{row.Sunday}</td>
                <td>{row.Monday}</td>
                <td>{row.Tuesday}</td>
                <td>{row.Wednesday}</td>
                <td>{row.Thursday}</td>
                <td>{row.Friday}</td>
                <td>{row.Saturday}</td>
            </tr>
        );
    })
    ) : (
            <tr>
                <td>failed</td>
            </tr>
        );

    return <tbody>{rows}</tbody>;
}

class Table extends Component {
    render() {
        const { recordData } = this.props;
        console.log("TCL: Table -> render -> recordData", recordData)

        return (
            <table>
                <TableHeader />
                <TableBody recordData={recordData} />
            </table>
        );
    }
}

export default Table