import React, { Component } from 'react'
import axios from 'axios'
import Table from './Table'
import Form from './Form'

class Home extends Component {
    state = {
        records: [],
    }

    componentDidMount() {

        let today = new Date()
        let sunday = today
        sunday.setUTCDate(sunday.getUTCDate() - sunday.getUTCDay())

        const sunday_date = sunday.toISOString().substring(0, 10)

        const url_string = 'http://www.hellapur.club/day/' + sunday_date

        console.log("TCL: Home -> componentDidMount -> url_string", url_string)


        axios.get(url_string)
            .then(res => {
                this.setState({
                    records: res.data
                });
            })
    }

    render() {
        const { records } = this.state

        return (
            <div className="container">
                <h3>where I am...</h3>
                <Table recordData={records} />
                <h3>Add New</h3>
                <Form />
            </div>
        )
    }
}

export default Home