import React, { Component } from 'react';
import Axios from 'axios';

class Form extends Component {
    constructor(props) {
        super(props);

        this.initialState = {
            name: '',
            date: '',
            location: ''
        };

        this.state = this.initialState;
    }

    handleChange = event => {
        const { name, value } = event.target;

        this.setState({
            [name]: value,
        });
    }

    onFormSubmit = (event) => {
        event.preventDefault();
        const url_string = 'http://www.hellapur.club/people/' + event.target.name.value
        const post_name = event.target.name.value
        const post_date = event.target.date.value
        const post_location = event.target.location.value

        Axios.post(url_string, {
            name: post_name,
            date: post_date,
            location: post_location
        })

        this.setState(this.initialState);
    }

    render() {
        const { name, date, location } = this.state;

        return (
            <form onSubmit={this.onFormSubmit} >
                <label>Name</label>
                <input type="text" name="name" value={name} onChange={this.handleChange} />
                <label>Date YYYY-MM-DD</label>
                <input type="text" name="date" value={date} onChange={this.handleChange} />
                <label>Location</label>
                <input type="text" name="location" value={location} onChange={this.handleChange} />
                <button type="submit">Submit</button>
            </form >
        );
    }

}

export default Form;